import { Carousel } from "react-responsive-carousel"

export default function ProjectCarousel ({ imageUrls }) {

    return(
        <div>
            <Carousel showThumb= {true} showArrows={true}>
                {imageUrls.map((imageUrl, index) => (
                    <div key={index}>
                        <img src={imageUrl} alt={`Slide ${index}`} />
                    </div>
                ))}
            </Carousel>
        </div>
    )
}